<template>
  <div class="radiation__wrapper">
    <p>{{activeMarker.name}}</p>
      <div v-for="item in activeMarker.mainInfo" :key="item.index" class="radiation__item">
        <p v-if="item.index_dump">Індекс скиду: {{item.index_dump}}</p>
        <p v-if="item.index_radioactive_releas">Показник радіоактивного викиду" {{item.index_radioactive_releas}}</p>
        <p v-if="item.co_60_dump">Скид Co-60: {{item.co_60_dump}}</p>
        <p v-if="item.cs_137_dump">Скид Cs-137: {{item.cs_137_dump}}</p>
        <p v-if="item.cs_137_emission">Викид Cs-137: {{item.cs_137_emission}}</p> 
        <p v-if="item.index_radioactive_releas">Показник радіоактивного викиду: {{item.index_radioactive_releas}}</p>
        <p v-if="item.irg">ІРГ: {{item.irg}}</p>
        <p v-if="item.irg_index">Індекс викидів ІРГ: {{item.irg_index}}</p>
        <p v-if="item.stable_radionuclides">ДІН: {{item.stable_radionuclides}}</p>
        <p v-if="item.volume">Об'єм: {{item.volume}}</p>
        <p v-if="item.year">Рік: {{item.year}}</p>
        <p v-if="item.quarter">Квартал: {{item.quarter}}</p>
      </div>
  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex';
export default {
  data: function() {
    return {
      viewer: {},
    }
  },

  created() {
    console.log('created rad')
  },
  mounted() {
    console.log('mounted rad')
  },
    methods: {
    ...mapActions([
      'addActiveMarkerRadiation'
    ]),
  },
  beforeDestroy() {
    this.addActiveMarkerRadiation({})
  },
  computed:
    mapState({
      activeMarker({radiation}) {
        return radiation.activeMarkerRadiation
      },
    }),
}
</script>

<style lang="scss" scoped>
.radiation {
  &__wrapper {
